<template>
  <div class="view aside">
    <c-aside @clickAside="APP.handleClickAside"></c-aside>
    <main class="main">
      <div class="tool-bar fixed">
        <el-button @click="save" type="primary" class="button" :disabled="!Boolean(APP.model.xm)">保存</el-button>
        <el-button @click="APP.remove" :disabled="!Boolean(APP.model.xm)">删除</el-button>
      </div>
      <div class="tool-bar"></div>
      <el-collapse v-model="activeNames">
        <!-- title 用于显示在页面上，name用于展开的collapse与activeNames配合 -->
        <el-collapse-item :title="getTitle(APP.dslArray[0].title)" :name="APP.dslArray[0].title">
          <c-table
            class="box-card"
            v-model="APP.model"
            :dsl="APP.dslArray[0].rows"
            :rules="APP.rules"
            :tableInfo="APP.dslArray[0]"
            ref="topTabe"
            :disabled="!Boolean(APP.model.xm)"
          />
        </el-collapse-item>
      </el-collapse>
    </main>
  </div>
</template>

<script>
import Main from "./Main.js";
import $ from "jquery";

export default {
  name: "FnbpcFkjc",
  mixins: [Main],
  inject: ["APP"],
  mounted() {
    this.APP.getAsideList();
    this.activeNames = this.APP.dslArray.map(panel => {
      return panel.title;
    });
    this.activeNames.push("datagrid");
  },
  data() {
    return {
      /* 动态数据 */
      asideTree: [],
      activeNames: []
    };
  },
  methods: {
    /* 完成刷新前的数据检测 */
    async save() {
      try {
        /* 验证表单是否都符合rule */
        if (await this.$refs.topTabe.valid()) {
          this.APP.save();
        } else {
          throw new Error("请按照提示检查输入项");
        }
      } catch (error) {
        this.$message({
          showClose: true,
          message: error.message,
          type: "warning"
        });
      }
    },
    getTitle(fix) {
      return this.APP.model.xm
        ? `${fix}-${this.APP.model.xm}-${this.APP.model.bh}`
        : fix;
    }
  }
};
</script>

