<template>
  <div id="app">
    <transition name="silde-fade">
      <router-view/>
    </transition>
  </div>
</template>

<script>
import { model, dslArray, dslAsideQuery, rules } from "./mock.js";

let MSG_OPTIONS = {
  message: "",
  type: ""
};

export default {
  name: "AppRoot",
  mounted() {
    console.log(this.model);
  },
  provide() {
    return {
      APP: this
    };
  },
  data() {
    return {
      modelInit: {} /* 用于reset的初始化数据 */,
      model,
      dslArray,
      rules,
      dslAsideQuery,
      /* 侧边树 */
      modelAsideQuery: { bh: "", xm: "" },
      modelAsideList: []
    };
  },
  computed: {
    treeOptions() {
      let treeOptions = {
        props: {
          children: "children",
          label: "xm"
        }
      };
      treeOptions.data = this.modelAsideList;
      return treeOptions;
    }
  },
  methods: {
    c(...args) {
      console.log.apply(console, args);
    },
    async save({ loading }) {
      /* loading框 */
      loading = loading || this.$loading();
      try {
        let doc = this.model;
        /* save */
        let response = await this.$post("/proxy/update", {
          coll: "fnbpc_jbzl" /* collection */,
          doc /* document */
        });
        if (response.isSuccess) {
          /* 刷新列表 */
          MSG_OPTIONS = {
            type: "success",
            message: "保存成功"
          };
          this.getAsideList();
        } else {
          throw new Error(response.data);
        }
      } catch (error) {
        MSG_OPTIONS = {
          type: "error",
          message: error.message
        };
      } finally {
        loading.close();
        this.$message({ showClose: true, ...MSG_OPTIONS });
      }
    },
    async remove({ loading }) {
      let isConfirm = await this.$confirm(`确实要删除当前的表单?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      });
      if (!isConfirm) return;
      loading = loading || this.$loading();
      let doc = this.model;
      try {
        let response = await this.$post("/proxy/delete", {
          coll: "fnbpc_jbzl" /* collection */,
          doc /* document */
        });
        if (response.isSuccess) {
          this.$message({
            showClose: true,
            message: "删除成功",
            type: "success"
          });
          this.model = {};
          this.getAsideList();
        } else {
          console.log(response.data);
          throw new Error("error");
        }
      } catch (error) {
        this.$message({
          showClose: true,
          message: error.message,
          type: "error"
        });
        loading.close();
      } finally {
        loading.close();
      }
    },
    async getAsideList() {
      const loading = this.$loading();
      let condition = this.modelAsideQuery;
      try {
        let { isSuccess, data } = await this.$post("/proxy/get", {
          coll: "fnbpc_jbzl" /* collection */,
          doc: {},
          condition
        });
        if (isSuccess) {
          this.modelAsideList = data;
        }
      } catch (error) {
        console.log(error);
      }
      loading.close();
    },
    handleClickAside({ data, node, vm }) {
      /* 不可修改下拉框数据，方便测试 */
      /*   this.model = this.$set(
        this,
        "model",
        Object.assign({}, this.modelInit, data, {
          zjys: String(data.zjys) + String(Date.now())
        })
      ); */
      this.model = Object.assign({}, this.modelInit, data, {
        cjrq: Date.now(),
        cjr: String(data.zjys) + String(Date.now()),
        bgrq: Date.now(),
        bgr: Date.now(),
        bbmyd: "满意"
      });
    }
  },
  watch: {
    model(newvalue) {
      console.clear();
      console.log("model", newvalue);
    }
  }
};
</script>

<style src="@c/styles/app.scss" lang="scss">
</style>
<style src="@c/styles/hqbj.scss" lang="scss">
</style>


