<template>
  <div class="view aside">
    <aside class="pickup">
      <c-pickup-tree :treeOptions="treeOptions" @check-change="handleCheckChange" ref="pickupTree"></c-pickup-tree>
      
    </aside>
    <main class="main">
      <div class="tool-bar fixed">
        <el-button @click="goTo('home')">放弃编辑</el-button>
        <el-button @click="save" type="primary" class="button">保存</el-button>
      </div>
      <div class="tool-bar"></div>
      <el-collapse v-model="activeNames" @change="handleChange">
        <!-- title 用于显示在页面上，name用于展开的collapse与activeNames配合 -->
        <el-collapse-item
          :title="panelDsl.title"
          :name="panelDsl.title"
          v-for="(panelDsl, panelDslIndex) in dslArray"
          :key="'panelDsl'+panelDslIndex"
        >
          <c-table
            class="box-card"
            v-model="currentModelAdd"
            :dsl="panelDsl.rows"
            :rules="rules"
            :tableInfo="panelDsl"
            ref="topTabe"
          />
        </el-collapse-item>
      </el-collapse>

      <el-collapse v-model="activeNames" @change="handleChange">
        <!-- title 用于显示在页面上，name用于展开的collapse与activeNames配合 -->
        <el-collapse-item title="项目列表" name="datagrid">
          <div id="datagrid">
            <el-table :data="currentTableModelAdd">
              <template v-for="(tableH, index) in tableHeader">
                <el-table-column
                  v-if="tableH.prop==='jcdh'"
                  :prop="tableH.prop"
                  :label="tableH.label"
                  :key="index"
                >
                  <template slot-scope="scope">
                    <el-form :model="scope.row" ref="dynamicValidateForm" class="demo-dynamic">
                      <el-form-item prop="jcdh" :rules="rules.jcdh">
                        <el-input v-model="scope.row.jcdh"></el-input>
                      </el-form-item>
                    </el-form>
                  </template>
                </el-table-column>
                <!-- 检查结果 -->
                <el-table-column
                  v-else-if="tableH.prop==='jg'"
                  :prop="tableH.prop"
                  :label="tableH.label"
                  :key="index"
                >
                  <template slot-scope="scope">
                    <el-form :model="scope.row" ref="dynamicValidateForm" class="demo-dynamic">
                      <el-form-item prop="jg" :rules="rules.jcdh">
                        <el-input v-model="scope.row.jg"></el-input>
                      </el-form-item>
                    </el-form>
                  </template>
                </el-table-column>
                <!-- 操作 -->
                <el-table-column
                  v-else-if="tableH.prop==='wh'"
                  :prop="tableH.prop"
                  :label="tableH.label"
                  :key="index"
                >
                  <template slot-scope="scope">
                    <el-button @click="deleteTableRow(scope.row,scope)">删除</el-button>
                  </template>
                </el-table-column>
                <el-table-column v-else :prop="tableH.prop" :label="tableH.label" :key="index"></el-table-column>
              </template>
            </el-table>
          </div>
        </el-collapse-item>
      </el-collapse>
    </main>
  </div>
</template>

<script>
import Main from "./Main.js";
import { dslArray, rules, asideTree } from "./mock.js";
import $ from "jquery";

export default {
  name: "HqbjHjHyjc",
  mixins: [Main],
  mounted() {
    /* get dslArray model from remote server
        and then set this.currentModelAdd this.dslArray
        */

    var vm = this;
    $.ajax({
      url: "/scha-web/a/fnbj/hys/asideList",
      success: function(result) {
        vm.asideTree = result.data;
      }
    });
    // this.asideTree = asideTree;

    this.activeNames = this.dslArray.map(panel => {
      return panel.title;
    });
    this.activeNames.push("datagrid");

    /* 模拟获取数据 */
    /*  this.$post("http://127.0.0.1:3000/n/test", {
          name: "asdfasdfsf",
          pwd: "asdfsf"
        }).then(response => {
          console.log(response);
        }); */

    /*     setTimeout(() => {
      this.asideTree = asideTree;
      this.setCurrentModelAdd(model);
      this.setcurrentImgListModel(imgListModel);
    }, 1);
 */
  },
  data() {
    return {
      rules,
      dslArray,
      /* 动态数据 */
      asideTree: [],
      activeNames: []
    };
  },
  methods: {
    goTo(command) {
      this.$router.push({
        name: command
      });
    },
    handleCheckChange() {
      let aTree = this.$refs.pickupTree.getTree();
      let isLeaf = true;
      let currentChecked = aTree.getCheckedNodes(isLeaf);
      let stampModelTable = [...this.currentTableModelAdd];

      currentChecked.map((checked, indexChecked) => {
        /* 如果已存在则跳过，不存在则增加 */
        for (let index = 0; index < stampModelTable.length; index++) {
          const element = stampModelTable[index];
          if (element.id === checked.id) {
            currentChecked[indexChecked] = element;
            return;
          }
        }
      });
      this.setCurrentTableModelAdd(currentChecked);
    },
    deleteTableRow(row, index) {
      console.log("deleteTableRow", row, index);
      let stampModelTable = [...this.currentTableModelAdd];

      let target;
      const indexDelete = stampModelTable.findIndex(d => d.id === row.id);
      target = stampModelTable.splice(indexDelete, 1);
      /* 发送target到后台处理删除请求 */
      console.log("delete", target);
      /* 发送target到后台处理删除请求 */
      this.setCurrentTableModelAdd(stampModelTable);
    },
    save() {
      var currentModel = this.currentModelAdd;
      var currentTableModel = this.currentTableModelAdd;
      var myDate = {
        "tbFnbjHysEntity":currentModel,
        "list":currentTableModel

      }
      console.log("save", this.currentModelAdd);
      console.log("save currentTableModelAdd", this.currentTableModelAdd);
      $.ajax({
        url: "/scha-web/a/fnbj/hys/addHyd",
        data: JSON.stringify({
          "BaoZhuang":myDate
        }),
        dataType:"json",  
		    contentType : 'application/json;charset=utf-8', //设置请求头信息 
        success: function(result) {
         alert('ok');
        }
      });

    }
  },
  computed: {
    treeOptions() {
      let treeOptions = {
        props: {
          children: "children",
          label: "mc"
        }
      };
      treeOptions.data = this.asideTree;
      treeOptions.showCheckbox = true;
      return treeOptions;
    }
  }
};
</script>

